/* Week6.css */

/* Container styles */
.week6-container {
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: #299d8a; /* Light blue background */
  }
  
  /* Task container styles */
  .task-container {
    margin: 20px;
    padding: 20px;
    border: 10px solid #ed8a00; /* Black border */
    border-radius: 10px;
  }
  
  /* Button styles */
  .toggle-button,
  .user-type-button {
    background-color: #098c92; /* Dark turquoise */
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 10px;
    transition: background-color 0.3s ease;
  }
  
  .toggle-button:hover,
  .user-type-button:hover {
    background-color: #b6e108; /* Light green on hover */
  }
  
  /* Component styles */
  .component {
    margin: 20px;
    padding: 20px;
    background-color: #009814; /* Dark red background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(193, 28, 177, 0.973);
  }
  
  /* Colored line */
  .colored-line {
    border: 1px solid #148385; /* Dark blue line */
    width: 100%;
    margin: 40px auto;
  }
  
  /* Student details container */
  .student-details-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 40px;
  }
  
  /* Student details card */
  .student-details {
    margin: 20px;
    padding: 20px;
    border: 3px solid #06c5a2; /* Red border */
    border-radius: 10px;
    background-color: #434fd7; /* Light yellow background */
    box-shadow: 0 4px 8px rgb(63, 4, 4);
    width: 220px;
  }
  
  /* Subjects list styles */
  .subjects-list ul {
    padding-left: 20px;
  }